#NavBar{
    background-color: var(--customGreen);
    height: 50px;
}
ul{
  list-style-type: none;
  height: 100%;
}
.containerNavBar{
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
}
li{
  align-content: center;
  justify-content: center;
  width: 100%;
}
.btnNavBar{
    font-size: 15px;
    background-color: var(--customGreen);
    color: white;
    width: 100%;
    height: 100%;
    display: block;
    text-align: center;
    text-decoration: none;
}
.btnNavBar:hover{
    background-color: var(--customGreenHover);
    cursor: pointer;
}

.dropbtn {
    display: none;
    background-color: var(--customGreen);
    color: #fff;
    font-size: 16px;
    border: none;
    cursor: pointer;
    height: 100%;
  }
  
  .dropdown {
    justify-content: center;
    align-content: center;
    display: inline-block;
  }
  
  .dropdown-content {
    display: none;
    position: relative;
    background-color: var(--customGreen);
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    height: 50px;
  }  
    
  .dropdown:hover .dropdown-content {
    display: block;
  }
  
  .dropdown:hover .dropbtn {
    background-color: var(--customGreenHover);
  }

  @media (max-width: 645px){
    .dropbtn{
        display: inline-block;
    }
    .hidable{
        display: none;
    }
}