#Content{
    background-color: var(--customLightBlue);
    height: 100%;
    margin: 4vh 8vw 4vh 8vw ;
    padding-right: 4vh;
    padding-bottom: 4vh;
    padding-left: 4vh;
    border-radius: 25px;
    display: flex;
    flex-direction: column;
}
.Title{
    padding-left: 1vw;
    margin-top: 8vh;
    margin-bottom: 16vh;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: #fff;
    font-size: 6rem;
    border-left: 14px solid var(--customGreen);
    border-style: solid;
    border-spacing: 0;
    display: inline-block;
}
.Description{
    text-align: start;
    display: flex;
    flex-direction: column;
    margin-bottom: 16vh;
    
}

p{
    display: inline;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: #fff;  
    font-size: 2rem;  
    font-weight: bold;
    margin-bottom: 8px;
}
#contact{
    display: flex;
    text-align: center;
    align-items: center;
    width: 100%;
    justify-content: start;
}
.background_logo{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 5px;
    border-radius: 50%;
    background-color: #54b688;
    width: 30px;
    height: 30px;
    vertical-align: center;
}
.Languages{
    justify-content: center;
    display: grid;
    grid-template-columns: 100px 100px 100px;
    grid-template-rows: 150px 150px;
    grid-gap: 50px 30px;
}



@media (max-width: 645px){
    html{
        font-size: 9px;
    }

    .Description{
        margin-top: 18vh;
        
    }
    #Content{        
        margin: 2vh 8vw 2vh 8vw ;
    }
}

@media (max-width: 615px){
    .Description{
        margin-top: 12vh;
        
    }
    #Content{        
        margin: 1vh 4vw 1vh 4vw ;
    }
}

@media (max-width: 435px){
    html{
        font-size: 7px;
    }

    .Description{
        margin-top: 8vh;
        
    }
    #Content{        
        margin: 1vh 4vw 1vh 4vw ;
    }
    
}

@media (max-width: 435px){
    html{
        font-size: 5px;
    }

    .Description{
        margin-top: 8vh;
        
    }
    #Content{        
        margin: 1vh 4vw 1vh 4vw ;
    }
    
}