#Card{
    display: flex;
    background-color: var(--customDarkBlue);
}
.imageCard{
    width: 100px;
    height: 100px;
}
#contentCard{
    align-content: center;
    display: flex;
    flex-direction: column;
    border-left: 6px solid var(--customGreen);;
    padding-left: 10px;
    width: 100%;
}
#titleCard{
    text-align: center;   
    font-weight: bold;
}
.bodyCard{
    color: #fff;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 14px;
}